.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.wrapper {
  padding: 0.5rem;
  margin: 50px auto;
  max-width: 600px;
  // width: 100%;
  box-shadow: 0px 1px 16px 6px rgba(34, 60, 80, 0.2);
  border-radius: 5px;
  background-color: whitesmoke;
}

.app_title {
  text-align: center;
  font-family: 'Oooh Baby', cursive;
  font-size: 2.5em;
}

.todoItem:hover {
  background-color:#8993a7;
  
}