@import url('https://fonts.googleapis.com/css2?family=Oooh+Baby&display=swap');


.form {
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
}

.form_input {
  margin-right: 0.5rem;
  height: 30px;
  width: 80%;
  border: none;
  border-radius: 3px;
  &:focus {
    border: none;
    outline: 2px solid #5487ff;
  }
}

.form_button {
  border: none;
  border-radius: 5px;
  background: rgb(63, 103, 235);
  width: 20%;
  height: 34px;
  color: whitesmoke;
  outline: none;
  cursor: pointer;
  font-size: 1em;
}

